import React from 'react'
import { connect } from 'react-redux'
import { fetchFilms, getFilms } from '../actions/FilmsActions'

export class Films extends React.Component {

  componentDidMount() {
    this.props.getFilms();
  }
  mouseOver(url) {
    let link = 'https://image.tmdb.org/t/p/w500/' + url;
    this.props.updateBG(link);
  }
  renderTemplate = () => {
    const { films, isLoading, error } = this.props
    console.log(films);
    if (films.error) {
      return <p className="error">Во время загрузки фото произошла ошибка</p>
    }

    if (films.isLoading) {
      return <p>Загрузка...</p>
    } else {
      return films.films.results.map((entry, index) => {
          const style = {
            backgroundImage: 'url(https://image.tmdb.org/t/p/w500/' + entry.poster_path + ')'
          }

        return <div key={index} className="photo">
          <a href="#" className="filmcards__element" onMouseOver={() => this.mouseOver(entry.poster_path)}>
            <div className="filmcards__image" style={style}></div>
            <h2>{entry.title}</h2>
            <p>iMDb: {entry.vote_average}</p>
          </a>
        </div>;
      })
    }
  }
  render() {
    return (
      <div className="container">
        <section className="filter">
          <h1>Фильмы</h1>
          <div className="filter__wrapper">
            <div className="filter__element">
              <p>Рейтинг не ниже</p>
              <a href="#">6,5</a>
            </div>
            <div className="filter__element">
              <p>Перевод не хуже чем</p>
              <a href="#">лицензия</a>
            </div>
            <div className="filter__element">
              <p>Сортировка по</p>
              <a href="#">рейтингу</a>
            </div>
            <div className="filter__element">
              <p>Жанр</p>
              <a href="#">любой</a>
            </div>
          </div>
        </section>
        <section className="filmcards">
          <div className="filmcards__wrapper">
            {this.renderTemplate()}
          </div>
        </section>
      </div>
    )
  }
}

const mapStateToProps = store => ({
  films: store.films.films,
  isLoading: store.films.isLoading,
  error: store.films.error
});

export default connect(mapStateToProps, {fetchFilms})(Films);


