export const GET_FILMS_BEGIN = 'GET_FILMS_BEGIN'
export const GET_FILMS_SUCCESS = 'GET_FILMS_SUCCESS'
export const GET_FILMS_FAILURE = 'GET_FILMS_FAILURE'

export function getFilms() {
  return dispatch => {
    dispatch(fetchFilmsBegin());
    return fetch("https://api.themoviedb.org/3/discover/movie?sort_by=popularity.desc&api_key=c1ed2e567c5cac632ce01cacd2618eb3")
      .then(handleErrors)
      .then(res => res.json())
      .then(json => {
        dispatch(fetchFilmsSuccess(json));
      })
      .catch(error => dispatch(fetchFilmsError(error)));
  }
}
export const fetchFilmsBegin = () => ({
  type: GET_FILMS_BEGIN,
})

export const fetchFilmsSuccess = films => ({
  type: GET_FILMS_SUCCESS,
  payload: {films}
})

export const fetchFilmsError = error => ({
  type: GET_FILMS_FAILURE,
  payload: {error}
})

export function fetchFilms() {
  return (dispatch) => {

  }
}

function handleErrors(response) {
  if (!response.ok) {
    throw Error(response.statusText);
  }
  return response;
}


