import { UPDATE_BACKGROUND } from '../actions/BackgroundActions'
const initialState = {
  backgroundUrl: 'https://image.tmdb.org/t/p/w600_and_h900_bestv2/syDObMIKfE6MI5w9DAlofSr0QQl.jpg'
}

export function backgroundReducer(state = initialState, action) {
  switch (action.type) {
    case UPDATE_BACKGROUND:
      return {
        ...state,
        backgroundUrl: action.payload.url
      }
    default:
      return state
  }
  return state;
}


