import React, { Component } from 'react'
import { connect } from 'react-redux'
import '../App.css'
import { Films } from '../components/Films'
import { Background } from '../components/Background'
import { Header } from '../components/Header'
import { getFilms } from '../actions/FilmsActions'
import { updateBackground } from '../actions/BackgroundActions'

class App extends Component {
  render() {
    const { filmsArray, backgroundUrl, getFilmsAction, getBackgroundAction } = this.props;
    return (
      <React.Fragment>
        <Background backgroundUrl={ backgroundUrl }/>
        <Header />
        <main>
          <Films
            films={ filmsArray }
            getFilms={ getFilmsAction }
            updateBG={getBackgroundAction}
          />
        </main>
      </React.Fragment>
    );
  }
}

const mapStateToProps = store => {
  return {
    filmsArray: store.films,
    backgroundUrl: store.background.backgroundUrl,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    getFilmsAction: () => dispatch(getFilms()),
    getBackgroundAction: url => dispatch(updateBackground(url)),
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(App);


