export const UPDATE_BACKGROUND = 'UPDATE_BACKGROUND'

export function updateBackground(url) {
  console.log(url);
  return dispatch => {
    dispatch(updatedBG(url));

  }
}
export const updatedBG = url => ({
  type: UPDATE_BACKGROUND,
  payload: {url}
})



