import { GET_FILMS_BEGIN, GET_FILMS_SUCCESS, GET_FILMS_FAILURE } from '../actions/FilmsActions'

const initialState = {
  isLoading: true,
  films: null,
  error: null,
}

export function filmsReducer(state = initialState, action) {
  switch (action.type) {
    case GET_FILMS_BEGIN:
      return {
        ...state,
        isLoading: true,
        error: null }
    case GET_FILMS_SUCCESS:
      return {
        ...state,
        isLoading: false,
        films: action.payload.films
      }
    case GET_FILMS_FAILURE:
      return {
        ...state,
        isLoading: false,
        error: action.payload.error,
        films: []
      }
    default:
      return state
  }
  return state;
}


